<template>
    <panel :config="config" class="panel-v-card clickable" :class="{high: activities.length === 0}" :style="{width: displayWidth}">
        <template slot="body" v-if="user">
            <div class="chevron-action" :class="{high: activities.length === 0}" :style="{left: 'calc(' + displayWidth + ' - ' + (['xs', 'xxs', 'sm'].includes($mq) ? 10 : 40) + 'px)'}"><font-awesome-icon icon="chevron-right" size="3x" /></div>
            <a :href="urlFor('public_profile', {'id': userProfileId})" :target="isIn3rdParty ? '_blank' : '_self'">
                <div class="col-xs-2 col-sm-2 col-md-2" v-if="tooSmall()">
                    <img :src="user.avatar" class="icon avatar" @error="avatarError"/>
                    <span class="registered_since">{{ getTrans('registered_since') }} <time :date="user.join_date_ts">{{ $moment.unix(user.join_date_ts).format('L') }}</time></span>
                </div>
                <div class="details col-xs-10 col-sm-10 col-md-10" :class="{'col-xs-12': ['xs', 'xxs'].includes($mq)}">
                    <h2><img :src="user.avatar" class="icon avatar fixed" @error="avatarError" v-if="['xsm', 'xs', 'xxs'].includes($mq)"/> {{ user.login_name }}</h2>

                    <p class="registered_since" v-if="['xsm', 'xs', 'xxs'].includes($mq)">{{ getTrans('registered_since') }} <time :date="user.join_date_ts">{{ $moment.unix(user.join_date_ts).format('L') }}</time></p>

                    <h4 class="icons">
                        <span class="icon_detail" v-tooltip="getTrans('nb_achievements')"><i class="fanet fanet-stack anet-achievements"></i>&nbsp;{{ achievementsCount }}</span>
                        &nbsp;&nbsp;&nbsp;
                        <span class="icon_detail" v-tooltip="getTrans('number_buddies').replace(/&#039;/, '\'')"><i class="fanet fanet-stack anet-buddies"></i>&nbsp;{{ user.nbbuddies }}</span>
                        &nbsp;&nbsp;&nbsp;
                        <span class="icon_detail" v-tooltip="getTrans('ranked_online_games')"><i class="fanet fanet-stack anet-online-game fa-2x"></i>&nbsp;{{ onlineGamesCount }}</span>
                        &nbsp;&nbsp;&nbsp;
                        <span class="icon_detail" v-tooltip="getTrans('number_games')"><i class="fanet fanet-stack anet-game-log"></i>&nbsp;{{ activities.length }}</span>
                    </h4>
                    <div :style="'display:flex;align-content:flex-start;align-items: baseline;' + (!!['xs', 'xxs'].includes(this.$mq) ? ' flex-wrap: wrap;' : '')">
                        <div class="last_data" :class="(!!['xxs'].includes(this.$mq) ? ' first_line' : ' midwith')" v-if="activities.length > 0 && games && Object.keys(games).length > 0">
                            <h6>{{ getTrans('last_activity') }}</h6>
                            <div class="row decal">
                                <div class="col-xxs-1 col-xs-1 col-sm-1 col-md-1 small"><img class="icon game small" :src="games[activities[0].game_id].icon" v-tooltip.bottom="games[activities[0].game_id].name"  /></div>
                                <div class="col-xxs-9 col-xs-9 col-sm-9 col-md-9 small">
                                    <h4>{{ games[activities[0].game_id].name }}</h4>
                                    <time :date="activities[0].last_used">{{ $moment.unix(activities[0].last_used).format('L HH:mm') }}</time>
                                </div>
                            </div>
                        </div>

                        <div class="last_data" :class="{midwith: !['xxs'].includes(this.$mq)}" v-if="activities.length > 0 && achievements && Object.keys(achievements).length > 0">
                            <h6>{{ getTrans('last_achievement') }}</h6>
                            <div class="row decal">
                                <div class="col-xxs-1 col-xs-1 col-sm-1 col-md-1 small"><img :src="getAchievementIcon(lastAchievement)" class="icon achievement small" :class="getAchievementIconClass(lastAchievement)"/></div>
                                <div class="col-xxs-9 col-xs-9 col-sm-9 col-md-9 small">
                                    <h4>{{ getAchievementFieldForCurrentLang(lastAchievement, 'name') }}</h4>
                                    <time :date="lastAchievement.awarded_ts">{{ $moment.unix(lastAchievement.awarded_ts).format('L HH:mm') }}</time>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </a>
        </template>
        <template slot="body" v-else>
            <LoadingComp></LoadingComp>
        </template>
    </panel>
</template>

<script>
import axios from 'axios';
import GenericMixin from '@mixins/generic';
import AchievementMixin from '@mixins/achievements';
import HTTP from '@/httpapi';
import Games from '@api/games';
import User from '@api/user';
import Achievement from '@classes/achievement';

export default {
    name: 'v-card',

    mixins: [GenericMixin, AchievementMixin],

    computed: {
        displayWidth () {
            return (this.config.width ? this.config.width : (this.$mq === 'sm' ? '96%' : (['xsm', 'xs', 'xxs'].includes(this.$mq) ? '100%' : '500px')));
        },

        getChevronPos () {
            let d = this.displayWidth;
            if (d.endsWith('px')) {
                return (parseInt(d) - 20) + 'px';
            } else {
                return 'calc(' + d + ' - 2em)';
            }
        },

        userProfileId () {
            return this.config.userProfile.id;
        },

        lastAchievement () {
            if (this.achievements) {
                return Object.keys(this.achievements).reduce((earlier, gid) => {
                    let tmp = this.achievements[gid].reduce((earlier, ach) => {
                        if ((!earlier || earlier.awarded_ts < ach.awarded_ts) && ach.type !== 'officer' && ach.texts) {
                            return ach;
                        }

                        return earlier;
                    }, earlier);

                    if (tmp && (!earlier || earlier.awarded_ts < tmp.awarded_ts) && tmp.texts) {
                        return new Achievement(tmp);
                    }

                    if (earlier) {
                        return new Achievement(earlier);
                    }

                    return null;
                }, null);
            }

            return null;
        },

        achievementsCount () {
            if (this.achievements) {
                return Object.keys(this.achievements).reduce((total, gid) => total + this.achievements[gid].length, 0);
            }

            return 0;
        },

        onlineGamesCount () {
            if (this.onlinegames.length > 0) {
                return this.onlinegames.reduce((total, og) => total + og.nbgames, 0);
            }

            return 0;
        }
    },

    methods: {
        userDetails () {
            const that = this;
            axios.all([
                User.details(this.userProfileId),
                Games.all()
            ]).then(axios.spread(function (userDetails, games) {
                that.games = games.data.games;
                that.activities = userDetails.data.user.userapps;
                that.achievements = userDetails.data.user.achievements;
                that.onlinegames = userDetails.data.user.onlinegames;
                that.user = userDetails.data.user;
            }));
        },

        tooSmall () {
            return !['xsm', 'xs', 'xxs'].includes(this.$mq);
        }
    },

    mounted () {
        HTTP.ifInitialized(this.userDetails);
    },

    data () {
        return {
            user: null,
            activities: [],
            achievements: null,
            onlinegames: [],
            games: null,
        };
    }
};
</script>

<style lang="scss" scoped>
@import '~sass/_variables.scss';
@import '~sass/text.scss';
@import '~sass/image.scss';
@import '~sass/grid.scss';

.panel.panel-v-card /deep/ {
    // padding: 0;
    // height: ;

    .panel-body {
        padding: 0;
    }

    a {
        color: #333;
        display: inline-block;
        position: relative;
        top: -40px;
        width: 100%;
    }
    .icon.avatar {
        border-radius: 50%;
    }
    .registered_since {
        font-size: 0.8em;
    }

    h4 {
        margin-bottom: 6px;

        &.icons {
            position: relative;
            top: -10px;
        }
    }

    .chevron-action {
        text-align:center;
        top: 60px;
        vertical-align: middle;
        float: left;

        &.high {
            top: 10px;
        }

        .fa-chevron-right {
            position: relative;
            top: 15px;
            color: lightgray;
            font-weight: bold;
        }
    }

    .icon_detail {
        white-space: nowrap;
    }

    .last_data {
        font-size: 0.9em;
        padding-left: 0;
        &.midwith {
            width: 50%;
        }

        &.first_line:nth-of-type(1) {
            position: relative;
            top: -10px;
        }

        .row {
            .col-md-1 {
                &.small {
                    padding-left: 0;
                }

                .achievement {
                    margin: 0;
                    &.ribbon {
                        &.small {
                            height: 20px;
                            position: relative;
                            top: 10px;
                        }
                    }
                }
            }

            .col-md-9 {
                white-space: nowrap;

                &.small {
                    padding-left: 30px;
                    padding-top: 4px;
                }
            }
        }
    }

    .clearboth {
        clear: both;
    }
}
.panel-v-card {
    &.clickable:hover {
        background-color: lightgray;
        cursor: pointer;

        .chevron-action .fa-chevron-right {
            color: white;
        }
    }

    &.high {
        padding-bottom: 0px;
    }
}

@include media-breakpoint-between(xxs, sm) {
    .panel.panel-v-card {
        .icon {
            &.avatar {
                &.fixed {
                    height: $icon-max-height;
                    width: $icon-max-height;
                }
            }
        }
    }
}

@include media-breakpoint-down(sm) {
    .panel.panel-v-card {
        .last_data.midwith {
            width: 100%;
        }
    }
}

@include media-breakpoint-down(xs) {
    .panel.panel-v-card {
        .chevron-action {
            top: 110px;
        }
    }
}

@include media-breakpoint-up(sm) {
    .panel.panel-v-card {
        .registered_since {
            float: left;
        }
    }
}
</style>



